import uniList from '../../components/uni-list-c/uni-list.vue';
import uniListItem from '../../components/uni-list-item-c/uni-list-item.vue';
import rattenkingDtpicker from '../../components/rattenking-dtpicker/rattenking-dtpicker.vue';
import xlist from '../../wxcomponents/xlist/xlist.vue';
import xlistInput from '../../wxcomponents/xlist/xlist-input.vue'

export default {
	data() {
		return {
			isConfirm:false,
			kiloPrice: '￥28',
			isAgreement: true,
			totalPrice: 9827,
			repairList: [{
				title: '屏幕损坏',
				price: '￥499'
			}, {
				title: '电池不续航',
				price: '￥499'
			}]
		}
	},
	components: {
		uniList,
		uniListItem,
		rattenkingDtpicker,
		xlist,
		xlistInput
	},
	methods: {
		onOrderDetail() {
			if (this.isAgreement) {
				uni.navigateTo({
					url: '../orderdetail/orderdetail'
				})
			}
			return
		},
		onConfirm() {
			this.isConfirm = !this.isConfirm
		},
		onNext(){
			if ( this.isConfirm){
				uni.navigateTo({
					url: '../bevip/bevip'
				})
			}
			return
		},
		onOrder(){
			if (this.isConfirm){
				uni.navigateTo({
					url: '../orderdetail/orderdetail'
				})
			}
		}
	}
}
